<template lang="html">
  <div class="complaint-form-page">
    <!-- 头部 -->
    <default-head header="投诉"></default-head>
    <!-- 表单 -->
    <form class="" action="#" method="post">
      <complaint-select :selectList="mainBody.select" text="投诉主体" data-vv-name="投诉主体" v-validate="'required'" data-vv-value-path="currValue"
        v-model="complainInfo.byUId" :defOption="mainBody.default"></complaint-select>
      <complaint-line leftText="名称" placeholder="用户姓名"
        v-model="complainInfo.realName" data-vv-name="用户姓名" v-validate="'required'" name="realName"
      ></complaint-line>
      <complaint-line leftText="电话" placeholder="用户电话号码"
        v-model="complainInfo.mobile" data-vv-name="用户电话号码" v-validate="'required'" name="mobile"
      ></complaint-line>
      <complaint-select :selectList="typeList.select" text="类型" data-vv-name="投诉类型" v-validate="'required'" data-vv-value-path="currValue"
        v-model="complainInfo.title" :defOption="typeList.default"></complaint-select>
      <form-item-line text="描述" padding="0 .22rem" marginTop=".1rem">
        <textarea class="textarea-des" v-model="complainInfo.content"
        data-vv-name="投诉描述" v-validate="'required'" name="content" placeholder="请输入您投诉的详情"></textarea>
      </form-item-line>
      <!-- 图片上传 -->
      <div class="img-upload clearfix">
        <div class="img-text fl">
          <p class="prove">图片证据</p>
          <p class="option">选填</p>
        </div>
        <div class="img-item fl">
          <label for="pic_load">
            <img :src="complainInfo.picArray[0] || defaultimg" alt="">
            <input @change="changeFile($event, 0)" type="file" id="pic_load" style="display:none">
          </label>
        </div>
        <div class="img-item fl">
          <label for="pic_load1">
            <img :src="complainInfo.picArray[1] || defaultimg" alt="">
            <input @change="changeFile($event, 1)" type="file" id="pic_load1" style="display:none">
          </label>
        </div>
      </div>
      <foot-btn @click.native="sumitComplain"></foot-btn>
    </form>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import complaintSelect from '@/components/complaintForm/complaintSelect'
import complaintLine from '@/components/complaintForm/complaintLine'
import formItemLine from '@/components/common/formItemLine'
import footBtn from '@/components/common/footBtn'
import defaultimg from '@/assets/img/recognize/ph@3x.png'

export default {
  data () {
    return {
      defaultimg,
      mainBody: {
        select: [{name: '用户', value: 0}, {name: '个体业主', value: 1}, {name: '企业', value: 2}],
        default: '请选择投诉主体'
      },
      typeList: {
        select: [
          {name: '信息不靠谱举报', value: '信息不靠谱举报'},
          {name: '商家服务态度恶劣', value: '信息不靠谱举报'},
          {name: '商家投诉不真实', value: '商家投诉不真实'},
          {name: '拨打商家电话不接听', value: '拨打商家电话不接听'},
          {name: '服务项目与事实不符', value: '服务项目与事实不符'}
        ],
        default: '请选择投诉类型'
      },
      complainInfo: {
        userId: utils.userId(),
        byUId: '',
        mobile: '',
        title: '',
        content: '',
        realName: '',
        userType: utils.getType(),
        picArray: []
      }
    }
  },
  components: { defaultHead, complaintSelect, complaintLine, formItemLine, footBtn },
  created () {
    this.initPage()
  },
  methods: {
    initPage () {
      var complainData = sessionStorage.getItem('complainData')
      if (complainData) {
        complainData = JSON.parse(complainData)
        this.complainInfo.realName = complainData.realName
        this.complainInfo.byUId = complainData.type
        this.complainInfo.mobile = complainData.mobile
        sessionStorage.removeItem('complainData')
      }
    },
    sumitComplain () {
      this.$validator.validateAll().then(() => {
        this.ajaxSaveComplain(this.complainInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    changeFile (e, index) {
      utils.uploadPic(e.target.files, (data) => {
        this.ajaxUploadPic({fileName: data[0].fileName, fileBase64: data[0].base64Img}, index)
      })
    },
    ajaxUploadPic (data, index) {
      api.uploadPic(data).then(response => {
        let result = response.body
        if (result.isSuccess) {
          this.complainInfo.picArray[index] = result.data
          this.complainInfo.picArray = this.complainInfo.picArray.concat()
        }
      })
    },
    ajaxSaveComplain (data) {
      api.saveComplain(data).then(response => {
        let result = response.body
        if (result.isSuccess && result.data) {
          this.$router.push('/complaintnotes')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.complaint-form-page {
  padding-top: .88rem;
  height: 100%;
  background: @whiteColor;
  .textarea-des {
    width: 5.66rem;
    height: 3.4rem;
    font-size: .28rem;
    color: @midleColor;
    padding: .1rem .1rem .1rem;
    border-radius: .05rem;
  }
  .img-upload {
    padding: 0 .22rem;
    .img-text {
      width: 1.42rem;
      font-size: .3rem;
      .option {
        color: @lightColor;
        margin: .2rem 0 0 .3rem;
      }
    }
    .img-item {
      img {
        width: 2rem;
        height: 1.2rem;
      }
      &:nth-child(3) {
        margin-left: .4rem;
      }
    }
  }

}

</style>



